<template>
    <v-container id="template">
        <v-btn  id="print" class="blue white--text mr-4 mb-5" @click="printReport()">Print</v-btn>
        <v-btn id="print" class="red white--text mb-5" to="/mutstokperkiraancpkb" link>Back</v-btn>
        <template id="template">
            <table>
                <tr>
                    <td style="padding-bottom: 40px;">
                        <table id="tabel" height="453px" width="755px">
                            <tr>
                                <td id="td" rowspan="11" width="188px" style="font-size: x-large; text-align: center;">Label</td>
                                <td id="td" colspan="3" style="text-align: center;font-size: large;"><b>LABEL PRODUK RUAHAN</b></td>
                            </tr>
                            <tr>
                                <td id="td" colspan="3"><img style="vertical-align: middle;" src="../../assets/logoNiagara1.png" class="center" ></td>
                            </tr>
                            <tr>
                                 <td id="td" colspan="3" style="font-size: xx-large; text-align: center;"><b> {{induk["Nama Barang"]}}</b></td>
                            </tr>
                            <tr>
                                <td id="td" style="font-size: x-large;"><b>Kode Barang</b></td>
                                <td id="td" colspan="2" style="font-size: x-large; text-align: center;"><b>{{this.induk["Kode Barang Jadi"]}}</b></td>
                            </tr>
                            <tr>
                                <td id="td" style="font-size: x-large;"><b>No Bets</b></td>
                                <td id="td" colspan="2" style="font-size: x-large; text-align: center;"><b>{{induk["No LP"]}}</b></td>
                            </tr>
                            <tr>
                                <td id="td" style="font-size: x-large;"><b>Tgl Produksi</b></td>
                                <td id="td" colspan="2" style="font-size: x-large; text-align: center;"><b>{{induk["TanggalFormat"]}}</b></td>
                            </tr>
                            <tr>
                                <td id="td" style="font-size: x-large;"><b>Jumlah</b></td>
                                <td id="td" colspan="2" style="font-size: x-large; text-align: center;"><b>{{induk["Masuk"]}} Liter</b></td>
                            </tr>
                            <tr>
                                <td id="td" style="font-size: x-large;"><b>No Lulus Uji</b></td>
                                <td id="td" colspan="2" style="font-size: x-large; text-align: center;"><b></b></td>
                            </tr>
                            <tr>
                                <td id="td" style="font-size: x-large;"><b>Tgl Lulus Uji</b></td>
                                <td id="td" colspan="2" style="font-size: x-large; text-align: center;"><b></b></td>
                            </tr>
                            <tr>
                                <td id="td" rowspan="2" colspan="3" style="font-size: x-large;"><br><b> Diuji Oleh</b></td>
                            </tr>
                            <tr>
                                
                            </tr>
                        </table>
                    </td>
                </tr>
            </table>
        </template>
    </v-container>
</template>
<script>
    export default {
        data() {
            return {
                date: new Date().toISOString().substr(0, 10),
                menu: false,
                menu1: false,
                menu2: false,
                noLP:'',
                jumlah: '',
                noCOA: '',
                noWadah: '',

                kadaluwarsa: '',
                tanggalAmbilContoh: '',
                oleh: '',
                jumlahPrint: 0,
                induk: '',
                dbelicpkbforms: [],
                spesifikasiforms: [],
                NoRules: [
                    value => (value || '').length == 4 || 'INPUTAN HARUS 4 Karakter',
                ],
                nomor:'',
                nomorUji:'',
                keyword: '',
                jumlahData: 1,
                load: false,
                laporanujibs: [],
            }
        },
        methods: {
            getData() {
                var uri = this.$apiUrl + 'mutstokcpkbperkiraans/header/' + this.$route.params.kode
                this.$http.get(uri).then(response => {
                    this.induk = response.data[0]
                    this.induk["Kode Barang Jadi"] = this.induk["Kode Barang"].slice(0,2) + this.induk["Kode Barang"].slice(4,6)
                })
            },
            printReport() {
                window.print()
            },
        },
        created() {
            this.getData();
        },
    }
</script>
<style scoped>
    @media print {
        #print {
            display: none;
        }

        #template {
            background-color: white
        }
      
    }
    #tabel{
        border-collapse: collapse;
    }
    #tabel #th, #td {
        border: 1px solid black;
    }
    #btm{
        border-bottom: 1px solid black;
        width: 150px;
    }
    .center {
        display: block;
        margin-left: auto;
        margin-right: auto;
        /* width: 50%; */
    }
</style>